@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&family=Roboto:wght@100;300;400;500;700;900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    height: 100vh;
    width: 100vw;
    font-family: "Roboto", sans-serif;
    position: relative;
    overflow-x: hidden;
    .dashboard {
      z-index: 1;
      display: grid;
      grid-template-columns: 15% 85%;
      height: 100vh;
      position: relative;
    }
    .shapes__1 {
      position: absolute;
      background-color: rgb(255, 0, 64);
      height: 10rem;
      width: 10rem;
      left: 15rem;
      top: 2rem;
      transform: rotate(30deg);
    }
    .shapes__2 {
      position: absolute;
      background-color: rgb(255, 0, 64);
      height: 10rem;
      width: 10rem;
      left: 35rem;
      top: 15rem;
      border-radius: 10rem;
    }
    .shapes__3 {
      position: absolute;
      background-color: rgb(255, 0, 64);
      height: 10rem;
      width: 10rem;
      left: 60rem;
      top: 20rem;
      border-radius: 10rem;
    }
  }

  @media only screen and (max-width : 900px) {
    body{
      .dashboard {
        z-index: 1;
        display: flex;
        height: 180vh;
        position: relative;
      }

      .shapes__1 {
       display: none;
      }
      .shapes__2 {
        display: none;
      }
      .shapes__3 {
        display: none;
      }
    }
  }
