@mixin expenses {
    .expenses {
      color: white;
      &__info {
        display: flex;
        justify-content: space-between;
        margin-bottom: 1rem;
      }
      .expenses__container {
        display: flex;
        justify-content: space-between;
        height: 250px;
        .expense {
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 0.3rem;
          img {
            height: 4rem;
            max-height: 4rem;
            overflow: hidden;
            border-radius: 2rem;
            transition: 0.4s ease-in-out;
            &:hover {
              transform: scale(1.1);
            }
          }
          h6 {
            color: #f94144;
          }
        }
      }
    }
  }
